.btn {
  font-size: 16px;
  padding: 1.5rem 5rem;
  border-radius: 7px;
  border: 0;
  background-color: rgb(251, 209, 175);
  cursor: pointer;
  transition: .2s ease-in-out;
}

.btn:hover {
  background-color: rgb(255, 204, 163);
}

.btn:disabled {
  background-color: #ababab;
  color: #fff;
}